<script>
export default {
  props: {
    docs: Array,
    isNewUpload: Boolean,
    tasks: Array
  },
  data() {
    return {
      currentDup: {},
      resolveAll: false,
      showDupModal: false,
      actions: [
        {
          icon: "icon-return-back",
          label: "Back",
          color: "orange",
          click: () => {
            this.close();
          }
        }
      ],
      newTasks: [],
      dupTasks: [],
      allowedTypes: [
        "application/pdf",
        "application/msword",
        "application/vnd.openxmlformats-officedocument.wordprocessingml.document",
        "image/jpeg",
        "image/png",
        "image/gif"
      ]
    };
  },
  computed: {
    hasErrors() {
      for (let i = 0; i < this.tasks.length; i++) {
        if (this.tasks[i].status == "error") return true;
      }
      return false;
    },
    isComplete() {
      for (let i = 0; i < this.tasks.length; i++) {
        if (this.tasks[i].status == "error") {
          this.tasks.splice(i--, 1);
        }
      }
    }
  },
  watch: {
    tasks(tasks) {
      if (tasks.length == 0) this.close();
    }
  },
  methods: {
    upload() {
      this.newTasks.forEach(t => {
        if (t.status === "error") return;
        this.tasks.push(t);
        this.$refs.uploader.upload(t);
      });
      this.newTasks = [];
      this.close();
    },
    dismiss() {
      for (let i = 0; i < this.tasks.length; i++) {
        if (this.tasks[i].status == "error") {
          this.tasks.splice(i--, 1);
        }
      }
    },
    close() {
      this.newTasks = [];
      this.$emit("close");
    },
    resolveTasks(method) {
      this.currentDup = this.dupTasks.shift();
      this.showDupModal = !!this.currentDup && !method;
      if (!this.currentDup) return;
      if (method) method();
      this.resolveAll = false;
    },
    resolveByOverwrite() {
      let existingDoc = this.docIndex[this.currentDup.filename];
      if (existingDoc && existingDoc.Id !== null) {
        let id = existingDoc.Id;
        this.currentDup.data.id = id;
      } else {
        let index = this.newTasks.findIndex(
          t => t.filename === this.currentDup.filename
        );
        this.newTasks.splice(index, 1);
      }
      this.resolveTasks(this.resolveAll ? this.resolveByOverwrite : null);
    },
    resolveByDuplicate() {
      let segments = this.currentDup.filename.split(".");

      let counter = 0;
      let ext = segments.pop();
      let filename = segments.join(".");
      let exists = false;

      do {
        let newName = `${filename} (${++counter}).${ext}`;

        this.currentDup.filename = newName;
        exists = false;

        for (let i = 0; i < this.newTasks.length; i++) {
          let task = this.newTasks[i];
          if (task != this.currentDup && task.filename == newName) {
            exists = true;
            break;
          }
        }
      } while (this.docIndex[this.currentDup.filename] || exists);

      this.resolveTasks(this.resolveAll ? this.resolveByDuplicate : null);
    },
    resolveByCancel() {
      let index = this.newTasks.indexOf(this.currentDup);
      this.newTasks.splice(index, 1);
      this.resolveTasks(this.resolveAll ? this.resolveByCancel : null);
    },
    onTasksCreated(tasks) {
      this.docIndex = {};
      this.docs.forEach(doc => {
        if (this.docIndex[doc.Name]) return;
        this.docIndex[doc.Name] = doc;
        tasks.forEach(task => {
          if (task.filename === doc.Name) {
            this.dupTasks.push(task);
          }
        });
      });

      var dupTask = tasks.filter(
        t => tasks.filter(x => x.filename === t.filename).length > 1
      )[0];

      if (dupTask && dupTask !== null) {
        let [task] = tasks.slice(-1);
        this.dupTasks.push(task);
      }

      this.resolveTasks();
    },
    onTaskComplete(task, xhr) {
      let result = JSON.parse(xhr.response);

      if (result.error) {
        task.status = "error";
        task.error = result.error;
        return;
      }

      let index = this.tasks.indexOf(task);
      this.tasks.splice(index, 1);

      if (!task.data.id) this.$emit("doc-created", result.doc);
      else this.$emit("doc-updated", result.doc);
    }
  }
};
</script>

<template>
  <tile title="Upload New Document" icon="icon-upload" class="upload-section" :actions="actions">
    <div class="section">
      <div class="section-body">
        <uploadlist
          v-show="isNewUpload"
          ref="selector"
          :tasks="newTasks"
          :accept="allowedTypes"
          @tasks-created="onTasksCreated"
        ></uploadlist>
        <uploadlist
          v-show="!isNewUpload"
          ref="uploader"
          method="POST"
          url="/Documents/Upload"
          @complete="$emit('complete')"
          @task-complete="onTaskComplete"
          :status-filter="['queued', 'sending', 'error']"
          :tasks="tasks"
          :can-add-tasks="false"
        ></uploadlist>
        <div class="section-bottom">
          <button v-if="isNewUpload && newTasks.length > 0" class="button green" @click="upload()">
            <span class="b-icon icon-upload"></span>
            <span>Upload</span>
          </button>
          <button v-if="!isNewUpload && hasErrors" class="button blue" @click="upload()">
            <span class="b-icon icon-cw"></span>
            <span>Retry All</span>
          </button>
          <button v-if="!isNewUpload && hasErrors" class="button blue" @click="dismiss()">
            <span class="b-icon icon-blocked"></span>
            <span>Dismiss Errors</span>
          </button>
        </div>
      </div>
    </div>
    <modal class="dup-modal" :visible="showDupModal">
      <p>
        {{ currentDup ? currentDup.filename : null }} already exists in your library.
        <br />You can either replace it, make a copy or cancel the upload.
      </p>
      <div class="modal-bottom">
        <label>
          <input type="checkbox" v-model="resolveAll" />Apply to all
        </label>
        <div class="buttons">
          <button class="orange" @click="resolveByOverwrite()">Replace</button>
          <button class="blue" @click="resolveByDuplicate()">Copy</button>
          <button class="grey" @click="resolveByCancel()">Cancel</button>
        </div>
      </div>
    </modal>
  </tile>
</template>

<style lang="scss">
.upload-section {
  .file-selector {
    padding: 75px;
  }

  .dup-modal .uimodalbody {
    position: relative;
    min-width: 350px;

    p {
      line-height: 1.5em;
    }

    .modal-bottom {
      position: absolute;
      left: 5px;
      right: 5px;
      bottom: 0;
      height: 30px;
      display: inline-block;

      label {
        position: absolute;
        display: inline-block;
        margin-right: 10px;
        left: 0;

        input {
          vertical-align: middle;
        }
      }

      .buttons {
        display: inline-block;
        position: absolute;
        right: 0;
      }
    }
  }
}
</style>
