<script>
import Vue from "vue";
import { debounce, throttle } from "lodash";

import EmpInfo from "./emp-info.vue";
import EmpList from "./emp-list.vue";

Vue.component("search-emp-info", EmpInfo);
Vue.component("search-emp-list", EmpList);

export default {
  props: {
    configHost: String
  },
  data() {
    return {
      query: null,
      queryValid: false,
      expanded: false,
      emp: null,
      seached: false,
      searching: false,
      employees: [],
      loading: true,
      searchRequest: null,
      infoConfig: []
    };
  },
  computed: {
    searchSummaryText() {
      return this.employees.length > 0
        ? `Found ${this.employees.length} employees`
        : "No results found.";
    }
  },
  methods: {
    loadConfig() {
      this.$ajax
        .post("/Config/StaffSearch")
        .then(resp => {
          if (resp && resp.data && resp.data.DisplayRows instanceof Array) {
            resp.data.DisplayRows.sort((a, b) => {
              return a.Order - b.Order;
            });
            this.infoConfig = resp.data.DisplayRows;
          }
        })
        .catch()
        .then(() => {
          this.loading = false;
        });
    },
    onSearchInput(evt) {
      if (this.query && this.query.length >= 2) {
        this.searching = true;

        if (this.searchRequest) {
          this.searchRequest.cancel();
        }

        this.searchRequest = this.$ajax.CancelToken.source();

        this.$ajax
          .post("/Search", `query=${this.query}`, {
            cancelToken: this.searchRequest.token
          })
          .then(resp => {
            this.employees = resp ? resp.data || [] : [];
            if (this.emp) this.emp.expanded = false;
            this.emp = null;
          })
          .catch()
          .then(() => {
            this.searched = true;
            this.searching = false;
            this.searchRequest = null;
          });
      } else {
        this.employees = [];
        this.searched = false;
      }
    },
    onListSelectionChange(emp) {
      this.emp = emp;
      if (!emp) return;
    }
  },
  created() {
    this.loadConfig();
    this.onSearchInput = debounce(this.onSearchInput, 500);
  }
};
</script>

<template>
  <tile
    title="Staff Directory Search"
    icon="icon-search"
    class="search"
    :class="{ expanded: emp }"
    :nopadding="true"
    :loading="loading"
  >
    <div class="section">
      <div class="section-body">
        <div class="default-form">
          <div class="form-row">
            <input
              type="text"
              placeholder="Search for an employee"
              class="search-editor"
              v-model="query"
              @input="onSearchInput"
            />
          </div>
          <div class="form-row">
            <div v-if="!searched && searching" class="search-result-summary">
              Searching...
            </div>
            <div v-else-if="searched" class="search-result-summary">
              {{ searchSummaryText }}
            </div>
          </div>
          <div class="form-row row-search-result" v-if="employees.length > 0">
            <div class="col-search-result">
              <search-emp-list
                :level="0"
                :max-level="2"
                :data="employees"
                :selection-mode="true"
                :info-config="infoConfig"
                @selection-change="onListSelectionChange"
              ></search-emp-list>
            </div>
            <div class="col-emp-info" v-if="emp !== null">
              <search-emp-info
                :level="1"
                :max-level="2"
                :data="emp"
                :config="infoConfig"
              ></search-emp-info>
            </div>
          </div>
        </div>
      </div>
    </div>
  </tile>
</template>

<style lang="scss">
.search {
  position: relative;
  display: inline-block;
  min-width: 400px;
  width: auto !important;

  .section {
    min-width: 200px;

    .form-row {
      margin: 0 !important;

      & + .form-row {
        margin-top: 10px !important;
      }
    }
  }
  .search-editor {
    width: 100% !important;
    box-sizing: border-box;
  }
  .search-result-summary {
    color: #333;
    display: inline-block;
    box-sizing: border-box;
    padding: 10px;
    width: 100%;
    background: #e0e0e0;
  }
  .row-search-result {
    font-size: 0;
  }
  .col-search-result,
  .col-emp-info {
    font-size: 12px;
    vertical-align: top;
    box-sizing: border-box;
  }
  .col-search-result {
    width: 370px;
    display: inline-block;

    .emp-info {
      display: none;
    }
    & > .emp-list > .emp {
      padding-left: 0;
    }
  }
  .col-emp-info {
    width: 370px;
    display: none;
    margin-left: 10px;
  }
  @media (min-width: 1024px) {
    &.expanded {
      width: 780px;
    }
    .col-search-result,
    .col-emp-info {
      max-height: 600px;
      overflow-y: auto;
    }
    .col-emp-info {
      display: none;
    }
    .col-search-result {
      & > .emp-list > .emp {
        & > .emp-summary {
          .h-icon-expand {
            display: block;
          }
          .v-icon-expand {
            display: none;
          }
        }
        &.expanded {
          & > .emp-summary {
            background: #9fe2ff;
          }
          & > .emp-info {
            display: block;
          }
        }
      }
    }
  }
}
</style>
