<script>
    import { mapActions } from 'vuex';

    export default {
        data() {
            return {
                submitting: false,
                current: '',
                next: '',
                confirm: '',
                error: ''
            }
        },
        methods: {
            ...mapActions([
                'addToast',
                'showRequestError'
            ]),
            save() {
                if (this.validate()) {
                    this.submitting = true;

                    this.$ajax.post('/Security/UpdatePassword', {
                        CurrentPassword: this.current,
                        NewPassword: this.next
                    })
                    .then(resp => {
                        if (resp.data.Success) {
                            this.current = '';
                            this.next = '';
                            this.confirm = '';
                            this.error = '';

                            this.addToast({
                                type: 'success',
                                title: 'Password is changed',
                                message: 'Password has been successfully changed.'
                            })
                        }
                        else this.addToast({
                            type: 'error',
                            message: resp.data.Error,
                            title: "Couldn't change the password"
                        });
                    })
                    .catch()
                    .then(() => {
                        this.submitting = false;
                    });
                }
            },
            validate() {
                let error = null;

                if (!this.current) error = "Current password is required."
                else if (!this.next) error = "New password is not entered."
                else if (!this.confirm || this.next != this.confirm) {
                    error = `Password is not confirmed${ this.confirm ? ' correctly' : '' }.`;
                }

                if (error) this.addToast({
                    type: 'error',
                    title: "Password validation failed",
                    message: error
                });

                return !error;
            }
        }
    }
</script>

<template>
    <tile title="Change Password" icon="icon-lock" class="change-password w-100">
        <div class="section">
            <div class="section-body">
                <div class="default-form">
                    <div class="form-row">
                        <label>Current Password:</label>
                        <input type="password" v-model="current" />
                    </div>
                    <div class="form-row">
                        <label>New Password:</label>
                        <input type="password" v-model="next" />
                    </div>
                    <div class="form-row">
                        <label>Confirm Password:</label>
                        <input type="password"  v-model="confirm" />
                    </div>
                    <div class="form-row row-bottom loader-box" v-if="submitting"></div>
                    <div class="form-row row-bottom" v-else>
                        <button class="button green" @click="save()"><span class="b-icon icon-save"></span><span>Save</span></button>
                    </div>
                </div>
            </div>
        </div>
    </tile>
</template>
