<script>
    import DocGrid from './grid.vue';
    import { icon, name, dateCreated, dateModified, buttons } from './columns';

    export default {
        props: {
            data: Array,
            loading: Boolean,
            actions: Array
        },
        computed: {
            columns() {
                let columns = [
                    icon(),
                    name(),
                    dateCreated(),
                    dateModified()
                ];

                let colButtons = buttons();
                colButtons.buttons.push({
                    icon: 'icon-trash',
                    color: 'red',
                    click: (row, start, done) => {
                        start();

                        this.$ajax.post('/Documents/Delete', {
                            id: row.Id,
                            category: row.Category
                        })
                        .then(resp => {
                            let index = this.data.indexOf(row);
                            this.data.splice(index, 1);
                        })
                        .catch()
                        .then(() => done());
                    }
                });

                columns.push(colButtons);

                return columns;
            }
        },
        components: {
            DocGrid
        }
    }
</script>

<template>
    <doc-grid title="My Own Documents" class="own-docs" icon="icon-archive" :data="data" :loading="loading" :columns="columns" :actions="actions"></doc-grid>
</template>

<style lang="scss">
    .own-docs {
        .ui-grid td.col-buttons {
            width: 180px;
        }
    }
</style>
