<script>
import { mapActions } from "vuex";
import Account from "./account.vue";

export default {
  data() {
    return {
      accounts: [],
      loading: true,
      submitting: false,
      country: "",
      counter: 0,
      maxAccounts: 0
    };
  },
  computed: {
    actions() {
      let actions = [];

      if (this.accounts.length < this.maxAccounts) {
        actions.push({
          icon: "icon-add-to-list",
          label: "Add an account",
          click: () => {
            this.addAccount();
          }
        });
      }

      return actions;
    }
  },
  methods: {
    ...mapActions(["addToast", "showRequestError"]),
    getAccounts() {
      this.$ajax
        .post("/Profile/GetBankAccounts")
        .then(resp => {
          this.country = resp.data.Country;
          this.accounts = resp.data.Accounts;
          this.maxAccounts = resp.data.MaxAccounts;
          (this.accounts || []).forEach(a => {
            a.id = ++this.counter;
          });
        })
        .catch()
        .then(() => {
          this.loading = false;
        });
    },
    addAccount() {
      this.accounts.push({
        Flag: "120"
      });
    },
    removeAccount(index) {
      this.accounts.splice(index, 1);
    },
    save() {
      let valid = true;

      (this.$refs.accounts || []).forEach(acc => {
        valid &= acc.validate();
      });

      if (valid) {
        this.submitting = true;
        this.$ajax
          .post("/Profile/SaveBankAccounts", JSON.stringify(this.accounts))
          .then(resp => {
            this.accounts[0].Flag = "120";

            this.addToast({
              type: "success",
              title: "Accounts are updated",
              message: "Accounts have been successfully updated."
            });
          })
          .catch(e => {
            this.showRequestError({
              resp: e.response,
              title: "Could not update accounts."
            });
          })
          .then(() => {
            this.submitting = false;
          });
      } else
        this.addToast({
          type: "error",
          title: "Cannot save accounts",
          message:
            "Some accounts have invalid information entered. Please correct all errors and try again."
        });
    },
    onAccountStateChange(account, state) {
      account.Flag = state ? "120" : "130";
    }
  },
  created() {
    this.getAccounts();
  },
  components: {
    Account
  }
};
</script>

<template>
  <tile
    class="bank-accounts"
    icon="icon-dollar-round"
    title="Bank Accounts"
    :actions="actions"
    :loading="loading"
  >
    <div class="section">
      <div class="section-body">
        <draggable class="default-form" :list="accounts" :options="{ handle: '.title-text' }">
          <div class="account" v-for="(a, index) in accounts" :key="a.id">
            <div class="title-row section-title" :class="{ primary: index == 0 }">
              <span class="title-text" v-if="index == 0">Primary Bank Account</span>
              <span class="title-text" v-else>Additional Bank Account {{ index }}</span>
            </div>
            <account
              :showAmount="index > 0"
              validationUrl="/Profile/ValidateBankAccount"
              :data="a"
              :country="country"
              ref="accounts"
            ></account>
            <x-switch
              class="acc-switch"
              v-if="index > 0"
              :value="a.Flag == '120'"
              @input="onAccountStateChange(a, $event)"
            ></x-switch>
            <button
              v-if="accounts.length > 1"
              class="btn-rem button red"
              @click="removeAccount(index)"
            >
              <span class="b-icon icon-trash"></span>
              <span>Remove</span>
            </button>
          </div>
        </draggable>
        <div class="section-bottom">
          <div class="loader-box" v-if="submitting"></div>
          <button class="button green" @click="save()" v-else>
            <span class="b-icon icon-save"></span>
            <span>Save</span>
          </button>
        </div>
      </div>
    </div>
  </tile>
</template>

<style lang="scss">
.bank-accounts {
  .title-row {
    margin: 0 0 15px 0 !important;

    .title-text {
      cursor: move;
    }
  }

  .account {
    position: relative;
    border: 1px dashed #aaa;
    padding: 10px;

    & + .account {
      margin-top: 10px;
    }

    .btn-rem {
      position: absolute;
      right: 15px;
      top: 15px;
      cursor: pointer;
    }

    .acc-switch {
      position: absolute;
      right: 105px;
      top: 14px;
    }
  }

  .section-bottom {
    margin-top: 30px;
  }
}
</style>
