<script>
    import { mapActions } from 'vuex';

    export default {
        data() {
            return {
                submitting: false,
                work: '',
                home: '',
                loading: true
            }
        },
        created() {
            this.$ajax.post('/Security/GetEmails')
                .then(resp => {
                    this.work = resp.data.Work;
                    this.home = resp.data.Personal;
                })
                .catch()
                .then(() => {
                    this.loading = false;
                });
        },
        methods: {
            ...mapActions([
                'addToast',
                'showRequestError'
            ]),
            save() {
                this.submitting = true;

                this.$ajax.post('/Security/UpdateEmails', {
                    Work: this.work,
                    Personal: this.home
                })
                .then(resp => {
                    if (!resp.data.Success) {
                        this.addToast({
                            type: 'error',
                            message: resp.data.Error,
                            title: "Couldn't update emails"
                        })
                    }
                    else this.addToast({
                        type: 'success',
                        title: 'Emails are updated',
                        message: 'Emails have been successfully updated.'
                    });
                })
                .catch()
                .then(() => {
                    this.submitting = false;
                });
            },
            validate() {

            }
        }
    }
</script>

<template>
    <tile title="Email Addresses" icon="icon-mail" class="w-100" :loading="loading">
        <div class="section">
            <div class="section-body">
                <div class="default-form">
                    <div class="form-row">
                        <label>Work Email:</label>
                        <input type="text" v-model="work" />
                    </div>
                    <div class="form-row">
                        <label>Personal Email:</label>
                        <input type="text" v-model="home" />
                    </div>
                    <div class="form-row row-bottom loader-box" v-if="submitting"></div>
                    <div class="form-row row-bottom" v-if="!submitting">
                        <button class="button green" @click="save()"><span class="b-icon icon-save"></span><span>Save</span></button>
                    </div>
                </div>
            </div>
        </div>
    </tile>
</template>
