<script>
import Vue from "vue";
import moment from "moment";
import { mapActions } from "vuex";

export default {
  data() {
    return {
      rules: [],
      view: "grid",
      loading: true,
      submitting: false,
      newRule: true,
      rule: {},
      approvers: [],
      approverEl: null,
      showInferiors: null,
      dateFrom: null,
      dateTo: null,
      areas: ["All", "Leave", "Timesheet", "Mileage Claims", "Clever Forms"],
      gridColumns: [
        {
          name: "DateFrom",
          header: "Date From",
          type: "date",
          format(value) {
            return moment(value).format("ll");
          }
        },
        {
          name: "DateTo",
          header: "Date To",
          type: "date",
          format(value) {
            return moment(value).format("ll");
          }
        },
        {
          name: "Type",
          header: "Type"
        },
        {
          name: "ApproverName",
          header: "Alternate Approver"
        },
        {
          name: "AreaName",
          header: "Notification Area"
        },
        {
          header: "",
          buttons: [
            {
              color: "orange",
              icon: "icon-pencil",
              text: "Edit",
              click: rule => {
                this.editRule(rule);
                this.newRule = false;
                this.view = "edit";
              }
            },
            {
              color: "red",
              icon: "icon-trash",
              text: "Delete",
              click: (rule, start, stop) => {
                start();
                this.delete(rule, stop);
              }
            }
          ]
        }
      ],
      gridActions: [
        {
          label: "Add rule",
          icon: "icon-plus",
          color: "blue",
          click: () => {
            this.newRule = true;
            this.editRule();
            this.view = "edit";
          }
        }
      ]
    };
  },
  computed: {
    selectedApprovers() {
      let approvers = [];

      this.approvers.forEach(a => {
        if (this.showInferiors) approvers.push(a);
        else if (a.IsDirect === null) approvers.push(a);
      });

      return approvers;
    }
  },
  watch: {
    showInferiors() {
      Vue.nextTick(() => {
        this.approverEl.updateOptions();
        if (this.rule) {
          this.approverEl.setValue(this.rule.ApproverId.toString());
        }
      });
    }
  },
  methods: {
    ...mapActions(["addToast", "showRequestError"]),
    parseResponse(resp, errTitle) {
      if (typeof resp == "string") {
        return JSON.parse(resp);
      } else
        this.showRequestError({
          resp,
          title: errTitle
        });
    },
    editRule(rule) {
      this.rule = rule
        ? Object.clone(rule)
        : {
            Type: "Share",
            Area: 0,
            ApproverId: "",
            DateFrom: null,
            DateTo: null
          };

      for (let i = 0; i < this.approvers.length; i++) {
        let approver = this.approvers[i];
        if (approver.EmployeeNo.toString() == this.rule.ApproverId) {
          this.showInferiors = approver.IsDirect !== null;
          break;
        }
      }

      this.dateFrom = this.rule.DateFrom
        ? moment(this.rule.DateFrom).toDate()
        : null;
      this.dateTo = this.rule.DateTo ? moment(this.rule.DateTo).toDate() : null;
    },
    save() {
      this.submitting = true;

      this.rule.DateFrom = moment(this.dateFrom).format("YYYY-MM-DD");
      this.rule.DateTo = moment(this.dateTo).format("YYYY-MM-DD");
      this.rule.ApproverId = this.approverEl.getValue();

      this.$ajax
        .post("/Approvals/SaveRule", this.rule)
        .then(resp => {
          var data = resp.data;

          if (!data.success)
            return this.showRequestError({
              resp: resp,
              title: "Couldn't save the rule"
            });

          var rule = JSON.parse(data.data);

          rule.AreaName = this.areas[rule.Area];
          let isNew = !this.rule.Id;

          if (!isNew) {
            let index = this.rules.indexOf(this.rule);
            this.$set(this.rules, index, rule);
          } else this.rules.push(rule);

          this.view = "grid";
          this.submitting = false;

          let op = isNew ? "added" : "updated";

          this.addToast({
            type: "success",
            title: `Rule is ${op}`,
            message: `Rule has been successfully ${op}.`
          });
        })
        .catch(resp => {
          this.showRequestError({
            resp,
            title: "Couldn't save the rule"
          });
        })
        .then(() => {
          this.submitting = false;
        });
    },
    delete(rule, cb) {
      this.$ajax.post("/Approvals/DeleteRule", { id: rule.Id }).then(resp => {
        let index = this.rules.indexOf(rule);
        this.rules.splice(index, 1);

        this.addToast({
          type: "success",
          title: "Rule is deleted",
          message: "Rule has been successfully deleted."
        });

        if (cb) cb();
      });
    },
    cancel() {
      this.view = "grid";
    },
    toggleInferiors() {
      this.showInferiors = !this.showInferiors;
    }
  },
  mounted() {
    this.$ajax
      .post("/Approvals/GetData")
      .then(resp => {
        let data = this.parseResponse(resp.data, "Couldn't retrieve rules");
        if (!data) return;

        this.rules = data.Rules || [];
        this.rules.forEach((r, i) => {
          r.AreaName = this.areas[r.Area];
        });

        this.approvers = data.Approvers || [];

        this.approverEl = new UIAutoCompleteWidget({
          selectElement: this.$refs.approverEl
        });
      })
      .catch(resp => {
        this.showRequestError({
          resp,
          title: "Couldn't retrieve rules"
        });
      })
      .then(() => {
        this.loading = false;
      });
  }
};
</script>

<template>
  <div class="approvals-manager">
    <tile
      v-show="view == 'grid'"
      title="Delegate Approvals"
      icon="icon-user-check"
      :actions="gridActions"
      :loading="loading"
    >
      <div class="section">
        <grid
          :columns="gridColumns"
          :data="rules"
          no-data-text="There are no rules defined"
        ></grid>
      </div>
    </tile>
    <tile
      v-show="view == 'edit'"
      :title="newRule ? 'Add an Approval Rule' : 'Edit Approval Rule'"
      :icon="newRule ? 'icon-plus-round' : 'icon-pencil'"
    >
      <div class="section">
        <div class="section-body">
          <div class="default-form">
            <div class="form-row">
              <label>From Date:</label>
              <datepicker allow-null v-model="dateFrom"></datepicker>
            </div>
            <div class="form-row">
              <label>To Date:</label>
              <datepicker allow-null v-model="dateTo"></datepicker>
            </div>
            <div class="form-row">
              <label>Type:</label>
              <select v-model="rule.Type">
                <option value="Share">Share</option>
                <option value="Delegate">Delegate</option>
              </select>
            </div>
            <div class="form-row">
              <label>Alternate Approver:</label>
              <div class="alt-approver-stack">
                <div class="alt-approver-box">
                  <select ref="approverEl">
                    <option
                      v-for="a in selectedApprovers"
                      :value="a.EmployeeNo"
                      :key="a.EmployeeNo"
                    >
                      {{ "[" + a.EmployeeNo + "] " + a.Name }}
                    </option>
                  </select>
                </div>
                <div class="filter-buttons">
                  <button
                    class="button icon-users-old"
                    title="Show Direct & Indirect Reports"
                    :class="{ active: showInferiors }"
                    @click="toggleInferiors"
                  ></button>
                </div>
              </div>
            </div>
            <div class="form-row">
              <label>Notification Area:</label>
              <select v-model="rule.Area">
                <option
                  v-for="(aname, index) in areas"
                  :value="index"
                  :key="index"
                  >{{ aname }}</option
                >
              </select>
            </div>
            <div class="form-row row-bottom loader-box" v-if="submitting"></div>
            <div class="form-row row-bottom" v-else>
              <button class="button green" @click="save()">
                <span class="b-icon icon-save"></span><span>Save</span>
              </button>
              <button class="button grey" @click="cancel()">
                <span class="b-icon icon-cancel"></span><span>Cancel</span>
              </button>
            </div>
          </div>
        </div>
      </div>
    </tile>
  </div>
</template>

<style lang="scss">
.approvals-manager {
  .ui-calendar-display {
    width: 325px !important;
    box-sizing: content-box;
  }
  .alt-approver-stack {
    font-size: 0;
    display: inline-block;
    line-height: 0;

    * {
      font-size: 12px;
    }
    .alt-approver-box {
      display: inline-block;
      line-height: 0;

      .ui-autocomplete-display {
        width: 307px !important;
      }
    }
    .filter-buttons {
      font-size: 0;
      display: inline-block;
      vertical-align: top;

      .button {
        font-size: 12px;
        margin: 0 0 0 2px;
        height: 25px;
        font-size: 14px;
        padding: 5px 7px;
        background: #888888;
        border: 0;

        &.active {
          background: #7abd2d;
        }
      }
    }
  }
}
</style>
