<script>
import { mapActions } from "vuex";

export default {
  data() {
    return {
      loading: true,
      options: {},
      answers: [{}, {}, {}],
      submitting: false
    };
  },
  methods: {
    ...mapActions(["addToast", "showRequestError"]),
    save() {
      this.loading = true;
      this.$ajax
        .post("/Security/SaveAnswers", this.answers)
        .then(resp => {
          if (resp.data.Success) {
            this.addToast({
              title: "Questions are saved",
              message: "Questions have been successfully saved.",
              type: "success"
            });
          } else {
            this.addToast({
              title: "Couldn't update questions",
              message: resp.data.Error,
              type: "error"
            });
          }
        })
        .catch()
        .then(() => {
          this.loading = false;
        });
    },

    handleSelect(e, answer, index) {
      let question = { ...this.options[Number(e.target.value) - 1] };
      question.AnswerId = answer.AnswerId;
      question.AnswerText = answer.AnswerText;
      this.$set(this.answers, index, question);
    }
  },

  mounted() {
    this.loading = true;
    this.$ajax
      .post("/Security/GetAnswers")
      .then(resp => {
        this.options = resp.data.Questions;
        this.answers = [];

        let i = 0;
        this.options.forEach(q => {
          if (q.AnswerId && i < 3) {
            this.$set(this.answers, i++, { ...q });
          }
        });

        this.answers.sort((a, b) => a.AnswerId - b.AnswerId);
      })
      .then(() => {
        this.loading = false;
      });
  }
};
</script>

<template>
    <tile title="Secret Questions" icon="icon-help-round" class="w-100 secret-questions" :loading="loading">
        <template v-for="(a, i) in answers">
            <tile :title="'Question ' + (i + 1)" icon="icon-question" class="w-100 question">
                <div class="section">
                    <div class="section-body">
                        <div class="default-form">
                            <div class="form-row">
                                <label>Question:</label>
                                <select :value="a.Id" @change="handleSelect($event, a, i)">
                                    <option v-for="q of options" :value="q.Id">{{ q.Text }}</option>
                                </select>
                            </div>
                            <div class="form-row">
                                <label>Answer:</label>
                                <input type="text" v-model="a.AnswerText" />
                            </div>
                        </div>
                    </div>
                </div>
            </tile>
        </template>
        <div class="section">
            <div class="section-body">
                <div class="default-form">
                    <div class="form-row row-bottom loader-box" v-if="submitting"></div>
                    <div class="form-row row-bottom" v-else>
                        <button class="button green" @click="save()"><span class="b-icon icon-save"></span><span>Save</span></button>
                    </div>
                </div>
            </div>
        </div>
    </tile>
</>
