import "babel-polyfill";
import Vue from './vue';
import Vuex from 'vuex';
import store from './store';
import Payslips from './payslips.vue';
import Approvals from './approvals.vue';
import Security from './profile/security/index.vue';
import Documents from './documents/index.vue';
import Search from './search/index.vue';
import BankAccounts from './profile/bank-accounts/index.vue';
import { mapActions } from 'vuex';

new Vue({
    el: '#app',
    store,
    methods: {
        ...mapActions([
            'closeToast'
        ]),
        onToastExpired(toast) {
            this.closeToast(toast);
        },
        onToastClick(toast) {
            this.closeToast(toast);
            if (toast.onClick) toast.onClick();
        }
    },
    components: {
        Payslips,
        Approvals,
        Security,
        Documents,
        BankAccounts,
        Search
    }
});
