<script>
    export default {
        props: {
            data: Array,
            title: String,
            icon: String,
            actions: Array,
            columns: Array,
            loading: Boolean
        },
        methods: {
            markRow(row) {
                return row.IsNew ? 'new' : '';
            }
        }
    }
</script>

<template>
    <tile :title="title" :icon="icon" :actions="actions" :columns="columns" :nopadding="true" :loading="loading" class="doc-grid">
        <div class="section">
            <div class="section-table nomargin">
                <grid load-sorted-by="DateCreated" :descending="true" :columns="columns" :data="data" :row-marker="markRow" no-data-text="There are no any files available." key-prop="Id"></grid>
            </div>
        </div>
    </tile>
</template>

<style lang="scss">
    .doc-grid {
        tr.new td {
            font-weight: bold !important;
        }
    }
</style>
