<script>
    import DocGrid from './grid.vue';
    import { icon, name, dateCreated, buttons } from './columns';

    export default {
        props: {
            data: Array,
            loading: Boolean
        },
        computed: {
            columns() {
                let dateShared = dateCreated();
                dateShared.header = 'Shared At';

                return [
                    icon(),
                    name(), {
                        name: 'OwnerName',
                        header: 'Shared By'
                    },
                    dateShared,
                    buttons()
                ];
            }
        },
        components: {
            DocGrid
        }
    }
</script>

<template>
    <doc-grid title="Shared With Me" icon="icon-share" :data="data" :loading="loading" :columns="columns"></doc-grid>
</template>
