import Vue from "vue";
import eventhub from "./eventhub";
import moment from "moment";
import fileIcons from "../../../../../../Web-UI/vue/upload/icons";

function getDocumentUrl(row) {
  return `/Documents/Download/${row.Id}?category=${row.Category}`;
}

function markDocumentAsViewed(state, row, start, done) {
  start();

  Vue.ajax
    .post("/Documents/MarkAsViewed", {
      id: row.Id,
      category: row.Category,
      value: state === null ? row.IsNew : state
    })
    .then(resp => {
      row.IsNew = state === null ? !row.IsNew : !state;
    })
    .catch()
    .then(() => done());
}

export function buttons() {
  return {
    header: "",
    buttons: [
      {
        color: "blue",
        mark: "btn-toggle-state",
        icon(row) {
          return row.IsNew ? "icon-tick" : "icon-flag";
        },
        click(row, start, done) {
          markDocumentAsViewed(null, row, start, done);
        }
      },
      {
        color: "blue",
        icon: "icon-download",
        text: "Download",
        href(row) {
          return getDocumentUrl(row);
        },
        click(row, start, done) {
          if (row.AcceptanceRequired && row.IsAccepted === null) {
            eventhub.$emit("acceptance-warning", {
              download: () => {
                window.location = getDocumentUrl(row);
                row.IsDownloaded = true;
              }
            });

            return false;
          } else row.IsNew = false;
        }
      }
    ]
  };
}

export function icon() {
  return {
    header: "",
    name: "icon",
    type: "icon",
    sortable: true,
    value(row) {
      let segments = row.Name.split(".");
      return segments[segments.length - 1] || "";
    },
    format(value, row) {
      return fileIcons[value.toLowerCase()] || "icon-page";
    }
  };
}

export function name() {
  return {
    name: "Name",
    sortable: true,
    type: "html",
    format(value, row) {
      let segments = value.split(".");
      segments.splice(segments.length - 1, 1);

      let name = `<span>${segments.join(".")}</span>`;
      if (row.AcceptanceRequired) {
        let icon = "icon-warning";

        if (row.IsAccepted === true) icon = "icon-thumbs-up";
        else if (row.IsAccepted === false) icon = "icon-thumbs-down";

        name = `<span class="acceptance-icon ${icon}"></span>` + name;

        let acceptance = "acceptance required";
        if (row.IsAccepted === true) acceptance = "accepted";
        else if (row.IsAccepted === false) acceptance = "declined";

        name += `<span class="acceptance-text ${
          row.IsAccepted ? "accepted" : "declined"
        }"> (${acceptance})</span>`;
      }

      return name;
    }
  };
}

export function dateCreated() {
  return {
    name: "DateCreated",
    header: "Created At",
    type: "date",
    sortable: true,
    format(value) {
      return value
        ? moment
            .utc(value)
            .local()
            .format("DD MMM YYYY hh:mm:ss a")
        : "";
    }
  };
}

export function dateModified() {
  return {
    name: "DateModified",
    header: "Last Modified At",
    type: "date",
    sortable: true,
    format(value) {
      return value
        ? moment
            .utc(value)
            .local()
            .format("DD MMM YYYY hh:mm:ss a")
        : "";
    }
  };
}
